import request from "../../utils/request.js";
export default {
	data() {
		return {
			navIndex:0,
			categoryDatas:[]
		};
	},
	async mounted(){
		const result = await request("/getCategoryDatas");
		this.categoryDatas = result;
	},
	computed:{
		categoryObj(){
			return this.categoryDatas[this.navIndex]
		}
	},
	methods:{
		changeNavIndex(index){
			this.navIndex = index;
		}
	}
}
